* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input{
  width: 100%;
  font-size: 1.2rem;
}

.card-grid{
  display: flex;
  font-size: 1.2rem;
  width: 100%;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10;
  margin-right: 10px;

}

.card p{
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}
